/* eslint-disable import/no-webpack-loader-syntax */
import React, { useEffect, useRef, useState } from 'react'
import 'mapbox-gl/dist/mapbox-gl.css'
// eslint-disable-next-line import/no-unresolved
import mapboxgl from '!mapbox-gl'
import './FlightMap.css'

const FlightMap = () => {
  mapboxgl.accessToken = 'pk.eyJ1IjoidG9tc21pdGg4MyIsImEiOiJjbDR3amg0dngwZnd2M3FxczAxdTJiYTNiIn0.MkEz6KZXSbiLcOBUv0dQZw'
  const mapContainer = useRef(null)
  const map = useRef(null)
  const [lng, setLng] = useState(122.56)
  const [lat, setLat] = useState(10.72)
  const [zoom, setZoom] = useState(7)
  useEffect(() => {
    if (map.current) return
    map.current = new mapboxgl.Map({
      container: mapContainer.current,
      style: 'mapbox://styles/mapbox/light-v10?optimize=true',
      center: [lng, lat],
      zoom,
    })
  })
  useEffect(() => {
    if (!map.current) return // wait for map to initialize
    map.current.on('move', () => {
      setLng(map.current.getCenter().lng.toFixed(4))
      setLat(map.current.getCenter().lat.toFixed(4))
      setZoom(map.current.getZoom().toFixed(2))
    })
  })
  useEffect(() => {
    map.current.on('load', () => {
      map.current.addSource('route', {
        type: 'geojson',
        data: {
          type: 'Feature',
          properties: {},
          geometry: {
            type: 'LineString',
            coordinates: [
              [122.56, 10.72],
              [120.98, 14.59],
            ],
          },
        },
      })
      map.current.addLayer({
        id: 'route',
        type: 'line',
        source: 'route',
        layout: {
          'line-join': 'round',
          'line-cap': 'round',
        },
        paint: {
          'line-color': '#888',
          'line-width': 8,
        },
      })
    })
  }, [])
  return (
    <div><div ref={mapContainer} className="map-container" /></div>
  )
}

export default FlightMap
